import React, { useState } from 'react';
import { useDispatch } from 'react-redux'; // Import useDispatch from react-redux
import { useNavigate } from 'react-router-dom'; // Import useNavigate from react-router-dom

import productsData from '../redux/Products/Products';
import Modal from 'react-bootstrap/Modal';
import { AiFillEye } from 'react-icons/ai';
import { addToCart } from '../redux/Cart/cartSlice'; // Import your addToCart action
import HommeProducts from './Products/category/homme/HommeProducts';
function Products() {
  const [showModal, setShowModal] = useState(false);
  const [selectedImage, setSelectedImage] = useState(null);
  const dispatch = useDispatch(); // Get the dispatch function from react-redux
  const navigate = useNavigate();

  const [selectedProduct, setSelectedProduct] = useState(null);
  const [selectedProductPath, setSelectedProductPath] = useState(null);
  // ...
  
  const openModal = (image) => {
    const product = productsData.find(item => item.OffersNo === image);
    setSelectedImage(image);
    setSelectedProduct(product);
    setShowModal(true);
  };

  const closeModal = () => {
    setShowModal(false);
  };
  const displayedProducts = productsData.slice(3, 9);

  const handleAcheterClick = (item) => {
    dispatch(addToCart(item));
    setSelectedProductPath(`/product/${item.OffersNo}`);
  };


  return (
    <div>
      <section className="product_section layout_padding">
        <div className="container">
          <div className="heading_container heading_center">
            <h2>
            Nos <span>Offres</span>
               
            </h2>
            <div className="row">
              {displayedProducts.map(product => (
                <div key={product.OffersNo} className="col-sm-6 col-md-4 col-lg-4">
                  <div className="box">
                    <div className="option_container">
                      {/* Add a button to open the modal */}
                      <button  className="acheter-button" onClick={() => openModal(product.OffersNo)}> Voir</button>



                    </div>
                    <div className="img-box">
                      <img
                        src={require(`../Componenets/BAImages/${product.OffersNo}.png`)}
                        alt={product.DescriptionOffre}
                      />
                      
                    </div>
                    <hr />
                    <hr />

                    <div className="detail-box">
                      <h5>{product.DescriptionOffre}</h5>

                      <h6>{product.PrixResultatOffre}DT</h6>

                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
        <div className="btn-box">
        <a href='/Offres'>
          View All products
        </a>
      </div>
      </section>

      {/* Modal */}
      <Modal show={showModal} onHide={closeModal} centered>
  <Modal.Header closeButton>
    <Modal.Title>Image Preview</Modal.Title>
  </Modal.Header>
  <Modal.Body>

    {selectedImage && (
      <img
        src={require(`../Componenets/BAImages/${selectedImage}.png`)}
        alt="Selected"
        className="modal-image"
      />
    )}
        <div className="modal-button-container">
      {/* Add your "Acheter" button here */}
      <button className="acheter-button" onClick={() => handleAcheterClick(selectedProduct)}>Acheter</button>
      <hr/>
      <button className="acheter-button"  onClick={() => navigate(`/product/${selectedProduct.OffersNo}`)}>View More</button>

    </div>
  </Modal.Body>
</Modal>
    </div>
  );
}

export default Products;

